import React from 'react';
import { Link } from 'react-router-dom';

const Home = () => {
  return (
    <div>
      <h1>Finance Tracker App</h1>
      <p>Welcome to the Finance Tracker App. Log in to manage your finances.</p>
      <Link to="/login">
        <button>Login</button>
      </Link>
      <p>Don't have an account?</p>
      <Link to="/signup">
        <button>Signup</button>
      </Link>
    </div>
  );
};

export default Home;
