import React, { useContext, useState } from "react";
import axios from 'axios';
import Savings from '../Components/Savings/Savings';

const BASE_URL = "http://localhost:5000/api/v1/";

const GlobalContext = React.createContext();

export const GlobalProvider = ({ children }) => {
    const [incomes, setIncomes] = useState([]);
    const [expenses, setExpenses] = useState([]);
    const [savings, setSavings] = useState([]);
    const [error, setError] = useState(null);

    // calculate incomes
    const addIncome = async (income) => {
        try {
            const response = await axios.post(`${BASE_URL}add-income`, income);
            getIncomes();
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const getIncomes = async () => {
        try {
            const response = await axios.get(`${BASE_URL}get-incomes`);
            setIncomes(response.data);
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const deleteIncome = async (id) => {
        try {
            const res = await axios.delete(`${BASE_URL}delete-income/${id}`);
            getIncomes();
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const totalIncome = () => {
        return incomes.reduce((total, income) => total + income.amount, 0);
    }

    // calculate expenses
    const addExpense = async (expense) => {
        try {
            const response = await axios.post(`${BASE_URL}add-expense`, expense);
            getExpenses();
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const getExpenses = async () => {
        try {
            const response = await axios.get(`${BASE_URL}get-expenses`);
            setExpenses(response.data);
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const deleteExpense = async (id) => {
        try {
            const res = await axios.delete(`${BASE_URL}delete-expense/${id}`);
            getExpenses();
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const totalExpenses = () => {
        return expenses.reduce((total, expense) => total + expense.amount, 0);
    };
    //Savings
    const addSavings = async (savings) => {
        try {
            const response = await axios.post(`${BASE_URL}add-savings`, savings);
            getSavings();
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const getSavings = async () => {
        try {
            const response = await axios.get(`${BASE_URL}get-savings`);
            setSavings(response.data);
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const deleteSavings = async (id) => {
        try {
            const res = await axios.delete(`${BASE_URL}delete-savings/${id}`);
            getSavings();
        } catch (err) {
            setError(err.response.data.message);
        }
    }

    const totalSavings = () => {
        if (!savings || savings.length === 0) {
            return 0;
        }
        return savings.reduce((total, savings) => total + savings.amount, 0);
    };
    

    const totalBalance = () => {
        return totalIncome() - totalExpenses();
    }


    const transactionHistory = () => {
        const history = [...incomes, ...expenses];
        history.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        return history;
    }

    return (
        <GlobalContext.Provider value={{
            addIncome,
            getIncomes,
            incomes,
            deleteIncome,
            expenses,
            totalIncome,
            addExpense,
            getExpenses,
            deleteExpense,
            totalExpenses,
            savings,
            addSavings,
            getSavings,
            deleteSavings,
            totalSavings,
            totalBalance,
            transactionHistory,
            error,
            setError
        }}>
            {children}
        </GlobalContext.Provider>
    );
}

export const useGlobalContext = () => {
    return useContext(GlobalContext);
  };


