// SavingsForm.js
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { useGlobalContext } from '../../context/globalContext';
import Button from '../Button/Button';
import { plus } from '../../utils/Icons';
import SharedInputStyles from '../../utils/SharedInputStyles';
function SavingsForm() {
  const { addSavings, error, setError, getSavings } = useGlobalContext();
  const [inputState, setInputState] = useState({
    title: '',
    amount: '',
    date: '',
    category: '',
    description: '',
  });

  const { title, amount, date, category, description } = inputState;

  useEffect(() => {
    getSavings();
  }, [getSavings]);

  const handleInput = (name) => (e) => {
    setInputState({ ...inputState, [name]: e.target.value });
    setError('');
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    addSavings(inputState);
    setInputState({
      title: '',
      amount: '',
      date: '',
      category: '',
      description: '',
    });
  };

  return (
    <SavingsFormStyled onSubmit={handleSubmit}>
      {error && <p className="error">{error}</p>}
      <div className="input-control">
        <SharedInputStyles
          type="text"
          value={title}
          name={'title'}
          placeholder="Savings Title"
          onChange={handleInput('title')}
          textColor="red" // Set the text color for "Savings Title"
          placeholderColor="red" // Set the placeholder color for "Savings Title"
        />
      </div>
      <div className="input-control">
        <SharedInputStyles
          type="text"
          value={amount}
          name={'amount'}
          placeholder={'Savings Amount'}
          onChange={handleInput('amount')}
          textColor="blue" // Set the text color for "Savings Amount"
          placeholderColor="blue" // Set the placeholder color for "Savings Amount"
        />
      </div>
      <div className="input-control">
        <DatePicker
          id="date"
          placeholderText="Enter A Date"
          selected={date}
          dateFormat="dd/MM/yyyy"
          onChange={(date) => {
            setInputState({ ...inputState, date: date });
          }}
        />
      </div>
      <div className="selects input-control">
        <select
          required
          value={category}
          name="category"
          id="category"
          onChange={handleInput('category')}
        >
          <option value="" disabled>
            Select Option
          </option>
          <option value="health">Health</option>
          <option value="emergency">Emergency Fund</option>
          <option value="vacation">Vacation Fund</option>
          <option value="others">Others</option>
        </select>
      </div>
      <div className="input-control">
        <textarea
          name="description"
          value={description}
          placeholder="Add A Reference"
          id="description"
          cols="30"
          rows="4"
          onChange={handleInput('description')}
        ></textarea>
      </div>
      <div className="submit-btn">
        <Button
          name={'Add Savings'}
          icon={plus}
          bPad={'.8rem 1.6rem'}
          bRad={'30px'}
          bg={'var(--color-accent'}
          color={'#fff'}
        />
      </div>
    </SavingsFormStyled>
  );
}

const SavingsFormStyled = styled.form`
  display: flex;
  flex-direction: column;
  gap: 1rem; /* Adjust the gap between input controls */

  .input-control {
    margin-bottom: 0.5rem; /* Adjust the margin bottom to reduce the gap */
  }

  .selects {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    select {
      width: 100%;
      padding: 0.5rem 1rem;
    }
  }

  .submit-btn {
    button {
      box-shadow: 0px 1px 15px rgba(0, 0, 0, 0.06);
      &:hover {
        background: var(--color-green) !important;
      }
    }
  }
`;

export default SavingsForm;
